@charset "UTF-8";
*,
*:before,
*:after {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
}
body {
  font-family: 'Roboto', sans-serif;
  color: #17324d;
  font-size: 100%;
  text-rendering: optimizeLegibility;
  -webkit-overflow-scrolling: touch;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  user-select: text;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  line-height: 1;
  background-color: #fbfbfb;
  margin: 0;
}
.strong {
  font-weight: 700;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
ol,
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  line-height: 1.25;
}
h1 {
  font-size: 1.5rem;
  color: #0a1f33;
}
h2 {
  font-size: 1.25rem;
  color: #0a1f33;
}
h3 {
  font-size: 1.125rem;
}
h4 {
  font-size: 1rem;
}
h5 {
  font-size: 0.875rem;
}
h6 {
  font-size: 0.75rem;
}
a {
  color: #5c7a99;
  text-decoration: none;
  transition: 0.1s color linear;
}
a:hover {
  color: #005fb2;
}
a:active {
  color: #00396b;
}
.svgwrapper .icon {
  display: inline-block;
}
iframe {
  border: none;
}
.footer {
  padding-top: 20px;
  border-top: 1px solid #d3d3d3;
  margin-top: 60px;
}
.dialog-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 10px;
  margin-bottom: 10px;
}
.root {
  width: '100%';
  maxWidth: 260;
  background: #efe9db;
}
.logo {
  width: 50px;
  height: 50px;
  cursor: pointer;
  margin-right: 10px;
}
.markdown {
  width: 100%;
  background-color: #f2f6fa;
}
.arc-section,
.bar-section,
.heatmap-section,
.area-section,
.label-section {
  display: flex;
  justify-content: center;
}
.heatmap-section {
  position: relative;
}
.colors iframe {
  width: 100%;
  height: 90vh;
}
.button-section h3,
.slider-section h3,
.appBar-section h3,
.progress-section h3,
.avatar-section h3,
.icon-section h3,
.list-section h3,
.rating-section h3,
.treeView-section h3 {
  margin: 18px 10px 18px 0;
  font-size: 20px;
}
.button-section .button,
.slider-section .button,
.appBar-section .button,
.progress-section .button,
.avatar-section .button,
.icon-section .button,
.list-section .button,
.rating-section .button,
.treeView-section .button,
.button-section .fab,
.slider-section .fab,
.appBar-section .fab,
.progress-section .fab,
.avatar-section .fab,
.icon-section .fab,
.list-section .fab,
.rating-section .fab,
.treeView-section .fab,
.button-section .extendedIcon,
.slider-section .extendedIcon,
.appBar-section .extendedIcon,
.progress-section .extendedIcon,
.avatar-section .extendedIcon,
.icon-section .extendedIcon,
.list-section .extendedIcon,
.rating-section .extendedIcon,
.treeView-section .extendedIcon,
.button-section .margin,
.slider-section .margin,
.appBar-section .margin,
.progress-section .margin,
.avatar-section .margin,
.icon-section .margin,
.list-section .margin,
.rating-section .margin,
.treeView-section .margin {
  margin: 5;
}
.button-section .section,
.slider-section .section,
.appBar-section .section,
.progress-section .section,
.avatar-section .section,
.icon-section .section,
.list-section .section,
.rating-section .section,
.treeView-section .section {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d3d3d3;
  padding-bottom: 20px;
}
.menu-section {
  margin: auto;
  display: flex;
  outline: 0;
  padding: 20px;
  position: relative;
  border-radius: 4px;
  justify-content: center;
  background-color: #f5f5f5;
}
.menu {
  position: absolute;
}
.appBar-section .text {
  padding: 20px 20px 0;
}
.appBar-section .paper {
  padding-bottom: 50px;
}
.appBar-section .list {
  margin-bottom: 20px;
}
.appBar-section .appBar {
  top: 'auto';
  bottom: 0;
}
.appBar-section .grow {
  flex: 1;
}
.appBar-section .fabButton {
  position: 'absolute';
  z-index: 1;
  top: -30px;
  left: 0;
  right: 0;
  margin: '0 auto';
}
.avatar-section .display {
  justify-content: space-around;
}
.avatar-section .avatar {
  margin: 0;
}
.avatar-section .bigAvatar {
  margin: 0;
  width: 60px;
  height: 60px;
}
.avatar-section .MuiAvatar-colorDefault.avatarFolder {
  margin: 10px;
}
.avatar-section .MuiAvatar-colorDefault.pinkAvatar,
.avatar-section .MuiAvatar-colorDefault.greenAvatar {
  margin: 10px;
  color: #fff;
  background-color: #ff3e96;
}
.avatar-section .MuiAvatar-colorDefault.greenAvatar {
  background-color: #00cd66;
}
.rating-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icons-section h3 {
  margin-bottom: 20px;
}
.icons-section ul {
  display: flex;
  flex-flow: wrap;
  overflow: scroll;
}
.icons-section ul li {
  cursor: pointer;
  padding: 10px;
  border: #fff 1px solid;
  display: flex;
  height: fit-content;
}
.icons-section ul li:hover {
  background-color: #e6ecf2;
  border: #c5d3e0 1px solid;
}
.icons-section ul li svg {
  width: 40px;
  height: 40px;
  fill: #5c7a99;
}
.modal-icon-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal-icon-title svg {
  width: 40px;
  height: 40px;
  fill: #5c7a99;
}
.App-Content {
  display: flex;
  flex-direction: row;
  margin-top: 30px;
}
.App-Content .hide {
  display: 'none';
}
.App-Content .content {
  width: 100%;
  margin: 0px 50px 10px 30px;
}
